@keyframes mercanie{
0%{
	 box-shadow: 0 0 26px #ffaa2f;
}

46%{
	 box-shadow: 0 0 26px #584948;

}

47%{
	 box-shadow: 0 0 26px #ffaa2f;
}
48%{
	 box-shadow: 0 0 26px #584948;
	 
}
49%{
	 box-shadow: 0 0 26px #ffaa2f;
}


