	 box-shadow: 0 0 50px #ffaa2f;
}

10%{
	 box-shadow: 0 0 20px #ffaa2f;
}

20%{
	 box-shadow: 0 0 50px #ffaa2f;

}

30%{
	 box-shadow: 0 0 20px #ffaa2f;
}

40%{
	 box-shadow: 0 0 50px #ffaa2f;

}

50%{
